.searchContainer {
    box-shadow: 10px 20px 10px rgba(0, 0, 0, 0.2);
    height: 200px;
    width: 100%;
    max-width: 490px;
    position: relative;
    top: 40px;
    left: 10px;
    background: #0a93c1; 
    background: linear-gradient(to right, #fff, #0a93c1); 
    border-radius: 3px;
}

.userInput {
    position: absolute;
    top: 28px;
    width: 456px;
    font-family: 'Poppins', sans-serif;
    padding: 5px 0px;
    text-align: center;

}

.formType {
    position: absolute;
    top: 90px;
    width: 120px;
    padding: 5px;
    border-radius: 5px;
    font-weight: 600;
    cursor: pointer;
    font-family: 'Poppins', sans-serif;
}

.distanceType {
    position: absolute;
    right: 50px;
    top: 92px;
    padding: 5px;
    border-radius: 5px;
    font-weight: 600;
    cursor: pointer;
    font-family: 'Poppins', sans-serif;
}

.searchBtn {
    position: absolute;
    left: 172px;
    bottom: 20px;
    padding: 3px 50px;
    border-radius: 5px;
    border: none;
    font-size: 16px;
    font-family: 'Poppins', sans-serif;
    cursor: pointer;
    box-shadow: 2px 5px 7px rgba(0, 0, 0, 0.3);
}

.searchBtn:hover {
    background: linear-gradient(to right, #0a93c1, #fff);
    color: black;
    border: 2px solid black;
}


/* Media Queries */

@media screen and (min-width:971px) and (max-width: 1048px) {
    .searchContainer {
        width: 35%;
    }
    .userInput {
        width: 89%;
        min-width: 200px;
    }
    .searchBtn {
        left: 95px;
    }
    .searchResults {
        width: 35%;
    }
}


@media screen and (min-width:676px) and (max-width: 970px) {
    .searchContainer {
        width: 98%;
