        top: 160px;
        max-width: 100vh;
    }
    .userInput {
        left: 26%;
    }
    .searchBtn {
        left: 42%;
    }
    
}

@media screen and (min-width:500px) and (max-width: 675px) {
    .searchContainer {
        max-width: 68vh;
    }
    .userInput {
        left: 24%;
        width: 50%;
    }
    .searchBtn {
        left: 37%;
    }
    
}

