.modal-wrapper {
    position: fixed;
    top: 0;
    height: 100vh;
    width: 100%;
    z-index: 7;
    transition: .7s ease-in-out;
}
.modal-background {
    position: fixed;
    top: 0;
    height: 100%;
    width: 100%;
    z-index: 2;
    background-color: rgba(0, 0, 0, 0.3);
}

.modal-box {
    position: relative;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: rgb(255, 255, 255);
    width: 40%;
    height: 100%;
    padding: 20px;
    overflow-y: auto;
    padding: 7px 10px;
    border-radius: 5px;
}

.modalHeader {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    color: black;
    text-decoration: underline;
    min-height: 8vh;
    margin-top: 25px;
    text-shadow: 10px 7px 13px rgba(0, 0, 0, 0.6);
}



.ModalPicture {
    width: 90%;
    height: 90%;
    box-shadow: 9px 14px 8px rgb(0,0,0,0.5) ,-8px -6px 8px rgb(0,0,0,0.5);
    padding: 20px;

}
.closeIcon {
    position: absolute;
    top: -7px;
    right: 16px;
    z-index: 1 ;
}
.closeBtn {
    font-size: 34px;
    cursor: pointer;
    color: #ED213A;
}

.modalContent {
    font-family: 'Poppins', sans-serif;
    letter-spacing: 1px;
    min-width: 370px;
    padding: 10px 10px;
}

.modalText {
    min-width: 300px;
    font-weight: 600;
    text-shadow: 10px 7px 13px rgba(0,0,0,0.6);
    letter-spacing: 1px;
    line-height: 32px ;
    padding-bottom: 35px;
}



.usedLanguages {
    font-family: 'Poppins', sans-serif;
    color: #272424;
    position: relative;
    text-decoration: underline;
    top: 5rem;
    text-shadow: 10px 7px 13px rgba(0,0,0,0.6);

}

.iconContainer {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    position: relative;
    top: 6rem;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.languagesIcon {
    width: 100px;
    height: 100px;
